.amplifier_img {
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
    position: relative;
    -webkit-transition: all .35s;
    transition: all .35s;  
}

.amplifier_mask {
    background: #fff;
    opacity: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-transition: all .35s;
    transition: all .35s;
    width: 100%;
}

.zoom-img-wrapper {
    position: relative;
    -webkit-transition: all .35s;
    transition: all .35s;
}

.amplifier_fade {
    opacity: 0.8;
     z-index: 1000;
}

.amplifier_scale {
    cursor: -webkit-zoom-out;
    cursor: zoom-out;
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
}

.zoom-img-wrapper.amplifier_maxIndex {
    z-index: 9999!important;
}


